import {
    Button,
    Container, 
    Card, 
    Image,
    Item
} from 'semantic-ui-react'; 


const parseSubjects = (props) => {
    let subjects = props.blog.category.map(item => {return <Item>{item}</Item> })
    return subjects
}
{/* <Item.Group divided>
{
 parseSubjects(props)
}
</Item.Group>> */}

const BlogCard = (props) => {
    return(
        <Card>
            <Card.Header>
                {props.blog.pubDate[0]}
            </Card.Header>
            <Card.Description>
                <h2>
                    {props.blog.title[0]}
                </h2>
            </Card.Description>
            <style jsx>
                {
                    `
                        p, h1, h2{
                            font-size: larger;
                            font-family:"Lucida Console", Monaco, monospace;
                        }
                    `
                }
                </style>
        </Card>
    )
}

export default BlogCard


